.conversation{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    cursor: pointer;
}

.conversationUser{
    display: flex;
    align-items: center;
}

.conversation:hover{
    background-color: rgb(230, 230, 230);
}

.conversationImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}
.conversationName{
    font-weight: 500;
}