.sidebar {
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: sticky;
    top: 50px;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(179, 179, 179);
}

.sidebarWrapper {
    /* background-color: var(--cBadgeNtf); */
    padding: 20px;
}

.sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;

}

.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    cursor: pointer;
    color: var(--cText);
}

.sidebarIcon {
    margin-right: 15px;
}

.sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;

}

.sidebarHr {
    margin: 20px 0;
    border-bottom: 1px groove var(--cText);
}

.sidebarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarUserId {
    font-size: 12px;
    color: black;
}

.sidebarUserPn {
    font-size: 12px;
    color: black;
    width: 80px;
    height: 13px;
    margin-left: 40px;
    margin-top: 5px;
    overflow: hidden;
}

.sidebarSettingsTitle {
    font-size: 14px;
    font-weight: 800;
    text-align: center;
    color: var(--cText);

}