.events {
    display: flex;
    color: var(--cText);
}
.eventsRight{
    flex: 9;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.rbc-event{
    cursor: pointer;
    border-radius: 5px;
    border: 2px solid var(--cText);
    background-color: transparent;
}

.rbc-row-segment .rbc-event-content{
    white-space: wrap;
    background-color: transparent;
    color: var(--cText);
    font-weight: 500;
}

